@import url('https://fonts.googleapis.com/css?family=Slabo+27px&subset=latin-ext');
#todo{
    height:40vh;
    width:33%;
    margin-right: auto;
    margin-left: auto;
    -webkit-transition-duration: 0.1s;
         -o-transition-duration: 0.1s;
            transition-duration: 0.1s;
    text-decoration: none;
    -webkit-animation: fadeIn .3s;
            animation: fadeIn .3s;

}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
#todoForm{
    margin-bottom: 5px;
    background-color : #222222;
}
input{
    background-color: #222222;
}
#notesInput{
margin-top: 3%;
    width: 100%;
    text-align: center;
    outline: none;
    font-family: 'Slabo 27px', serif;
    font-size:2em;
    border-bottom: 1px solid #ebdbb2;
    border-right-style: none;
    border-left-style: none;
    border-top-style: none;
    color: #ebdbb2;

}
.outerDivTodo{
    height:10%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:justify;
        justify-content:space-between;
    -ms-flex-align: center;
        align-items: center;
    /*border: 1px solid black;*/
    background-color: inherit;
    -webkit-animation: fadeInUp .75s;
            animation: fadeInUp .75s;
    padding-bottom: 7px;
    border-bottom: 1px solid #ebdbb2;
    margin:10px;
}
.noteText{
    padding-top:5px;
    max-height: 110%;
    width:90%;
    border: none;
    resize: none;
    /*next two lines: no cursor */
    font-family: 'Slabo 27px', serif;
    color: transparent;
    text-shadow: 0 0 0 #ebdbb2;
    font-size: 1.5em;
    background-color: transparent;

}

@-webkit-keyframes fadeInUp {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}



